.step-number {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  background-color: white;
  color: #747477;
  text-align: center;
  margin-bottom: 5px;
  margin: 1em;
  border: 1px solid #D1D1D2;
}

.bg-eeee77 {
  background-color: #EEEE77;
  color: black;
  border: none;
}

